<template>
    <div>
        <identify-card-page
            v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"
            :disable-read-card="true"
        />
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>

        <acceptance v-if="currentStep === steps.findIndex(step=>step.key === 'acceptance')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />

        <person-info v-if="currentStep === steps.findIndex(step=>step.key === 'personInfo')"/>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import personInfo from './components/person-info';
import acceptance from './components/acceptance';
import process from '@/components/process';
import { getIdCardProcess } from '@/service/human/identify-card';
import { LOCATION_ID } from '@/constant/constant';

export default {
    name: 'IdentityCardHandleProcessingIndex', // 身份证办理进度查询
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        personInfo,
        acceptance,
        process
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setSteps']),
        ...mapMutations('idNumHandleProcess', ['setProcessInfo']),
        async handleBusiness () {
            this.errorMsg = '';
            const res = await getIdCardProcess({});
            if (res && res.code === '0') {
                this.setProcessInfo(res.result);
                this.changeStep('down');
            } else {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        }
    },
    mounted () {
        const data = [
            {
                name: '身份识别',
                isActive: true,
                visible: true,
                key: 'identifyCardPage'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'faceYitu'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'contrast'
            },
            {
                name: '查询结果',
                isActive: false,
                visible: true,
                key: 'personInfo'
            }
        ];

        if (this.CONSTANT.location === LOCATION_ID.yinchuan || this.CONSTANT.location === LOCATION_ID.zhongwei) {
            data.splice(-1, 0, {
                name: '查询进度',
                isActive: false,
                visible: true,
                key: 'process'
            });
        } else {
            data.splice(-1, 0, {
                name: '输入受理号',
                isActive: false,
                visible: true,
                key: 'acceptance'
            });
        }

        this.setSteps(data);
    }
};

</script>
