<template>
    <div>
        <div class="kd-page-title">
            请输入受理号
        </div>
        <div class="kd-page-content-wrapper">
            <kd-full-input
                :ime-type="3"
                class="inputClass"
                placeholder="请输入身份证受理编号"
                :watch-data="getValue"
            />
            <div class="error-tip">
                {{ errorMsg }}
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-duihao2"
                text="提交"
                :button-handle="nextStep"
                :but-disable="!handleNum"
            />
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import { getIdCardProcess } from '@/service/human/identify-card';
import { keyboardHide } from '@/service/common';
import audioObj from '@/assets/media/household/15.mp3';
import { playAudio } from '../../../../../utils/media';

export default {
    name: 'IdentityCardHandleProcessingAcceptance',
    data () {
        return {
            handleNum: '',
            errorMsg: ''
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('idNumHandleProcess', ['setProcessInfo']),
        getValue (value) {
            this.handleNum = value;
        },
        async nextStep () {
            if (this.handleNum) {
                this.errorMsg = '';
                const res = await getIdCardProcess({
                    slh: this.handleNum // 受理号
                });
                if (res && res.code === '0') {
                    this.setProcessInfo(res.result);
                    this.changeStep('down');
                } else {
                    this.changeTipPage({
                        message: res.message,
                        enableBack: true
                    });
                }
            } else {
                this.errorMsg = '请输入有效的受理编号';
            }
        }
    },
    mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioObj.play();
    },
    beforeDestroy () {
        this.audioObj.destroy();
        keyboardHide();
    }
};
</script>

<style lang="scss" scoped>
    .error-tip {
        color: red;
        font-size: 24px;
        margin-top: 10px;
        text-align: center;
        height: 25px;
    }

    .inputClass {
        display: inline-block;
        height: 60px;
        width: 50vw;
        line-height: 50px;
        font-size: 26px;
        color: #636F93;
        margin: 118px 220px auto;
    }
</style>
